// youtube
import YouTube from "react-youtube";
import "../Hot.css";
import "../reset.css";
import { useState } from "react";
import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { Routes, Route, Link } from "react-router-dom";
import { khotdata, hotdata } from "../chart.js";
// import styled from "styled-components";
// import App from "../App.js";

function Hot(props) {
  let [music, setMusic] = useState(hotdata);
  let [khot, setMusic2] = useState(khotdata);
  return (
    <div id="wrap">
      <Navbar expand="lg" className="nav-bg">
        <Container id="nav-container">
          <Link to="/" className="logo">
            ActMusic
          </Link>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav>
              <Link to="/hot" className="color-white link">
                Hot
              </Link>

              <Link to="/Y2k" className="link">
                Y2K
              </Link>

              <Link to="/Hiphop" className="link">
                Hiphop
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <div className="void">
        <div className="mainV">
          <div className="mainV-text">
            <p id="mainV-text-t1">  1</p>
            <p id="mainV-text-t2">Beautiful Things(2024)</p>
            <p id="mainV-text-t3">
              Tik Tok       .
            </p>
          </div>
          <div className="youtubebox" id="mainV-App-youtube1">
            <YouTube
              videoId="Oa_RSwwpPaA" // 
              opts={{
                width: "100%",
                height: "500px",
                playerVars: {
                  autoplay: 0, //  
                  modestbranding: 1, //     
                },
              }}
              onReady={(e) => {
                e.target.mute(); // 
              }}
            />
          </div>
        </div>
      </div>

      <div className="list">
        <h2 className="title"> Hot</h2>
        <div className="Music-list">
          {music.map(function (a, i) {
            return <Hotlist music={music[i]} i={i} key={i}></Hotlist>;
          })}
        </div>
      </div>

      <div className="list">
        <h2 className="title"> Hot</h2>
        <div className="Music-list">
          {khot.map(function (a, i) {
            return <Khotlist music={khot[i]} i={i} key={i}></Khotlist>;
          })}
        </div>
      </div>

      <footer></footer>
    </div>
  );
}

function Hotlist(props) {
  const imgUrl = process.env.PUBLIC_URL;
  let [modal, setModal] = useState(false);

  return (
    <div className="Music-Content">
      {/* -------------------------------*/}

      <div id="album-img">
        <p
          id="album-bg"
          onClick={() => {
            setModal(true);
          }}
        ></p>
        <img src={"/public_assets/hot" + (props.i + 1) + ".jpg"}></img>
      </div>
      <p className="t1">{props.music.title}</p>
      <p className="t2">{props.music.singer}</p>

      {/* <p className="t2">{props.music.content}</p> */}

      {modal === true ? (
        <div className="Modal">
          <div
            id="Modal-bg"
            onClick={() => {
              setModal(false);
            }}
          ></div>

          <div id="Modal-box">
            <YouTube
              videoId={props.music.videoid} // 
              opts={{
                width: "100%",
                height: "550px",
                playerVars: {
                  autoplay: 0,
                  modestbranding: 1, //   
                },
              }}
              // onReady={(e) => {
              //   e.target.mute();
              // }}
            />
          </div>
        </div>
      ) : null}
    </div>
  );
}

