function Musicli(props) {
  const imgUrl = process.env.PUBLIC_URL;
  const [modal, setModal] = useState(false);

  return (
    <div className="Music-Content">
      <div id="album-img">
        <p
          id="album-bg"
          onClick={() => {
            setModal(true);
          }}
        ></p>

        {/* -------------------------------*/}

        <img src={"/public_assets/album" + (props.i + 1) + ".jpg"}></img>
      </div>
      <p className="t1">{props.music.title}</p>
      <p className="t2">{props.music.singer}</p>

      {/* <p className="t2">{props.music.content}</p> */}

      {modal === true ? (
        <div className="Modal">
          <div
            id="Modal-bg"
            onClick={() => {
              setModal(false);
            }}
          ></div>

          <div id="Modal-box">
            <YouTube
              videoId={props.music.videoid} // 
              opts={{
                width: "100%",
                height: "550px",
                playerVars: {
                  autoplay: 0,
                  modestbranding: 1, //   
                },
              }}
              // onReady={(e) => {
              //   e.target.mute();
              // }}
            />
          </div>
        </div>
      ) : null}
    </div>
  );
  // Musicli--
}
