import YouTube from "react-youtube";

import "../y2k.css";
import "../reset.css";
import { useState } from "react";
import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";
// import App from "../App.js";

import { y2kdata, y2kdata2 } from "../chart.js";

function Y2k(props) {
  let navigate = useNavigate();
  let [y2k, setY2k] = useState(y2kdata);
  let [y2k2, setY2k2] = useState(y2kdata2);
  return (
    <div id="wrap">
      <Navbar expand="lg" className="nav-bg">
        <Container id="nav-container">
          <h1
            to="/"
            className="logo"
            onClick={() => {
              navigate("/");
            }}
          >
            ActMusic
          </h1>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav>
              <Link
                to="/hot"
                className="color-white link"
                onClick={() => {
                  navigate("/hot");
                }}
              >
                Hot
              </Link>

              <Link to="/Y2K" className="link">
                Y2K
              </Link>
              <Link to="/Hiphop" className="link">
                Hiphop
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div className="void">
        <div className="mainV-y2k">
          <div className="mainV-text">
            <p id="mainV-text-t1">Y2K</p>
            <p id="mainV-text-t2">Sk8er Boi (2002)</p>
            <p id="mainV-text-t3">
              Y2K! 2000     !
            </p>
          </div>
          <div className="youtubebox" id="mainV-App-youtube1">
            <YouTube
              videoId="TIy3n2b7V9k" // 
              opts={{
                width: "100%",
                height: "500px",
                playerVars: {
                  autoplay: 0, //  
                  modestbranding: 1, //     
                },
              }}
              onReady={(e) => {
                e.target.mute(); // 
              }}
            />
          </div>
        </div>
      </div>

      {/* list */}
      <div className="list">
        <h2 className="title">Y2K  </h2>
        <h4>2000     </h4>
        <div className="Music-list">
          {y2k.map(function (a, i) {
            return <Y2klist y2k={y2k[i]} i={i} key={i}></Y2klist>;
          })}
        </div>
      </div>

      <div className="list">
        <h2 className="title">Y2K  </h2>
        <h4>2000     </h4>
        <div className="Music-list">
          {y2k2.map(function (a, i) {
            return <Musicli2 y2k={y2k2[i]} i={i} key={i}></Musicli2>;
          })}
        </div>
      </div>

      <footer></footer>
    </div>
  );
}
function Y2klist(props) {
  const imgUrl = process.env.PUBLIC_URL;
  let [modal, setModal] = useState(false);

  return (
    <div className="Music-Content">
      {/* -------------------------------*/}

      <div id="album-img">
        <p
          id="album-bg"
          onClick={() => {
            setModal(true);
          }}
        ></p>
        <img src={"/public_assets/y2kpop" + (props.i + 1) + ".jpg"}></img>
      </div>
      <p className="t1">{props.y2k.title}</p>
      <p className="t2">{props.y2k.singer}</p>

      {/* <p className="t2">{props.music.content}</p> */}

      {modal === true ? (
        <div className="Modal">
          <div
            id="Modal-bg"
            onClick={() => {
              setModal(false);
            }}
          ></div>

          <div id="Modal-box">
            <YouTube
              videoId={props.y2k.videoid} // 
              opts={{
                width: "100%",
                height: "550px",
                playerVars: {
                  autoplay: 0,
                  modestbranding: 1, //   
                },
              }}
              // onReady={(e) => {
              //   e.target.mute();
              // }}
            />
          </div>
        </div>
      ) : null}
    </div>
  );
}

function Musicli2(props) {
  const imgUrl = process.env.PUBLIC_URL;
  let [modal, setModal] = useState(false);

  return (
    <div className="Music-Content">
      {/* -------------------------------*/}

      <div id="album-img">
        <p
          id="album-bg"
          onClick={() => {
            setModal(true);
          }}
        ></p>
        <img src={"/public_assets/y2korea" + (props.i + 1) + ".jpg"}></img>
      </div>
      <p className="t1">{props.y2k.title}</p>
      <p className="t2">{props.y2k.singer}</p>

      {/* <p className="t2">{props.music.content}</p> */}

      {modal === true ? (
        <div className="Modal">
          <div
            id="Modal-bg"
            onClick={() => {
              setModal(false);
            }}
          ></div>

          <div id="Modal-box">
            <YouTube
              videoId={props.y2k.videoid} // 
              opts={{
                width: "100%",
                height: "550px",
                playerVars: {
                  autoplay: 0,
                  modestbranding: 1, //   
                },
              }}
              // onReady={(e) => {
              //   e.target.mute();
              // }}
            />
          </div>
        </div>
      ) : null}
    </div>
  );
  // Musicli--
}

export default Y2k;
