import { configureStore, createSlice } from "@reduxjs/toolkit";

// createSlice({
//   name: "State",
//   initialState: "",
// });

let user = createSlice({
  name: "emo",
  initialState: "nice",
});

let modal = createSlice({
  name: "modal",
  initialState: false,
});

export default configureStore({
  reducer: {
    user: user.reducer,
    modal: modal.reducer,
  },
});
